.App {
  text-align: center; }

body {
  font-family: "Roboto",sans-serif;
  text-rendering: inherit;
  text-rendering: initial;
  text-rendering: unset; }

.dogComponent {
  position: relative;
  display: inline-block;
  max-width: 480px;
  min-height: 500px;
  border-radius: 5px;
  margin-top: 300px; }

.dogComponent.male {
  background: #84C6FF; }

.dogComponent .dc-image {
  position: relative;
  width: 600px;
  height: 500px;
  margin: 0px auto;
  margin-top: -276px;
  background-image: url("../img/dog.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  pointer-events: none;
  left: -39px; }

.dogComponent .randomize-btn {
  background: #84C6FF;
  border: none;
  -webkit-box-shadow: 2px 10px 30px 8px #5DAFF6;
          box-shadow: 2px 10px 30px 8px #5DAFF6;
  color: #fff;
  padding: 20px 100px;
  border-radius: 90px;
  font-size: 20px;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 700;
  letter-spacing: .5px;
  -webkit-transition: 0.5s all;
  -o-transition: 0.5s all;
  transition: 0.5s all;
  outline: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.24); }

.dogComponent .randomize-btn:hover {
  -webkit-box-shadow: 2px 18px 40px 8px rgba(93, 175, 246, 0.61);
          box-shadow: 2px 18px 40px 8px rgba(93, 175, 246, 0.61);
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px); }

.dogComponent #display_dog_name {
  color: #fff;
  font-size: 100px;
  font-weight: 900;
  margin-top: 30px; }



